<template>
  <div>
   <div v-show="isLoading">
     載入中...
   </div>
    <div class="result" v-show="!isLoading">
       <div class="unit">
         <div class="parameter">
            <h3>全區域案件</h3>
         </div>
         <div class="parameter alignRight">
           <h3>相類似判決合計：{{dataTotalNum}}份</h3>
         </div>
       </div>
        <div class="section">
          <div class="unit">
            <div class="parameter">
              <p class="mr-0">預估一審判決時間</p>
              <h2 class="mr-0">{{year}}年{{month}}月{{day}}日</h2>
            </div>
          </div>
          <div class="unit">
            <div class="parameter alignVerticleTop">
              <div class="unit" v-for="(label, labelindex) in chartData1.labels" :key="labelindex" v-show="labelindex<3" :class="{highlighted: labelindex==0}">
                <div class="parameter">
                  {{label}}
                </div>
                <div class="parameter alignRight" v-for="(datas, index) in chartData1.datasets" :key="index">
                  <span v-for="(item, index) in datas.data" :key="index" v-if="index == labelindex">{{item}}%</span>
                </div>
              </div>
              <div class="unit" v-for="(label, labelindex) in chartData1.labels" :key="labelindex" v-show="labelindex>=3" v-if="showMoreData1">
                <div class="parameter">
                  {{label}}
                </div>
                <div class="parameter alignRight" v-for="(datas, index) in chartData1.datasets" :key="index">
                  <span v-for="(item, index) in datas.data" :key="index" v-if="index == labelindex">{{item}}%</span>
                </div>
              </div>
              <div class="unit">
                <div class="parameter">
                  <a href="javascript:;" @click="showMoreData1=true" v-if="!showMoreData1">顯示其他結果</a>
                </div>
              </div>
            </div>
            <div class="chart">
              <line-chart :chart-data="chartData1" :options="options"></line-chart>
            </div>
          </div>
        </div>
        <div class="section">
          <div class="unit">
            <div class="parameter">
              <p class="mr-0">預估判決結果</p>
              <h2 class="mr-0">拘役20天</h2>
            </div>
          </div>
          <div class="unit">
            <div class="parameter alignVerticleTop">
              <div class="unit" v-for="(label, labelindex) in chartData2.labels" :key="labelindex"  v-show="labelindex<3" :class="{highlighted: labelindex==0}">
                <div class="parameter">
                  {{label}}
                </div>
                <div class="parameter alignRight" v-for="(datas, index) in chartData2.datasets" :key="index">
                  <span v-for="(item, index) in datas.data" :key="index" v-if="index == labelindex">{{item}}%</span>
                </div>
              </div>
              <div class="unit" v-for="(label, labelindex) in chartData2.labels" :key="labelindex" v-show="labelindex>=3" v-if="showMoreData2">
                <div class="parameter">
                  {{label}}
                </div>
                <div class="parameter alignRight" v-for="(datas, index) in chartData2.datasets" :key="index">
                  <span v-for="(item, index) in datas.data" :key="index" v-if="index == labelindex">{{item}}%</span>
                </div>
              </div>
              <div class="unit">
                <div class="parameter">
                  <a href="javascript:;" @click="showMoreData2=true" v-if="!showMoreData2">顯示其他結果</a>
                </div>
              </div>
            </div>
            <div class="chart">
              <line-chart :chart-data="chartData2" :options="options"></line-chart>
            </div>
          </div>
        </div>
        <div class="section">
         <div class="unit">
          <div class="unitAction">
            Pro專屬
          </div>
          <div class="parameter">
            <hr>
          </div>
        </div>
         <div class="unit">
           <div class="parameter">
             <h3>{{location}}的案件分析</h3>
           </div>
         </div>
          <div class="unit">
            <div class="parameter">
              <p class="mr-0">緩刑或易科罰金機率</p>
              <h2 class="mr-0">？%</h2>
            </div>
          </div>

        </div>
      </div>
  </div>
</template>

<script>
import LineChart from '@/LineChart.js'
import 'chartjs-plugin-labels'

export default {
  props: {
    eventDate: '',
    eventArea: '',
    eventDist: ''
  },

  mounted () {
    setTimeout(this.loading, 1000)
    setTimeout(this.loadChart1, 1000)
    setTimeout(this.loadChart2, 1000)
    var date = this.eventDate
    date.setMonth(date.getMonth() + 18)
    this.month = date.getMonth()
    this.day = date.getDay()
    this.year = date.getFullYear()
    this.location = this.eventArea + this.eventDist
  },
  methods: {
    loading () {
      this.isLoading = false
    },
    loadChart1 () {
      this.chartData1 = {
        labels: [
          '1年6個月', '1年', '6個月（含以下）', '2年（含以上）', '1年9個月', '1年3個月'
        ],
        datasets: [{
          label: 'Data One',
          backgroundColor: ['#b89341', '#726e63', '#272727', '#726e63', '#272727', '#726e63'],
          data: [54, 31, 11, 3, 1, 0]
        }]
      }
    },
    loadChart2 () {
      this.chartData2 = {
        labels: [
          '拘役20天', '無罪', '有期徒刑4個月', '拘役40天', '有期徒刑3月', '有期徒刑6月', '有期徒刑1年', '有期徒刑2年', '有期徒刑2年6月', '有期徒刑3年'
        ],
        datasets: [{
          label: 'Data One',
          backgroundColor: ['#b89341', '#726e63', '#272727', '#726e63', '#272727', '#726e63', '#272727', '#726e63', '#272727', '#726e63'],
          data: [21, 19, 14, 12, 10, 9, 6, 5, 2, 1]
        }]
      }
    }
  },
  components: {
    LineChart

  },
  data () {
    return {
      dataTotalNum: '19,533',
      location: '',
      year: '',
      month: '',
      day: '',
      isLoading: true,
      showMoreData1: false,
      showMoreData2: false,
      chartData1: {
        labels: [],
        datasets: []
      },
      chartData2: {
        labels: [],
        datasets: []
      },
      options: {
        legend: {
          display: false
        },
        plugins: {
          labels: {
            render: 'percentage',
            fontColor: 'white',
            precision: 2
          }
        }
      }
    }
  }
}

</script>

<style lang="less">
  .chart {
    max-width: 250px;
    margin-left: 100px;
  }
  .result {
    font-size: 1.3em;

  }
</style>



