<template>
  <div id="app">
    <Header />
    <div class="container">
      <SideBar
      @modalStatus="onModalStatusGet"
      @eventDate="onEventDateGet"
      @eventArea="onEventAreaGet"
      @eventDist="onEventDistGet"
      />
      <router-view
      :eventDate="eventDate"
      :eventArea="eventArea"
      :eventDist="eventDist"
      ></router-view>
    </div>
    <Modal v-if="showModal" @close="showModal = false">
    <!--
      you can use custom content here to overwrite
      default content
    -->
    <h3 slot="header">custom header</h3>
    </Modal>
  </div>
</template>
<script>
import Header from '@/components/Header.vue'
import SideBar from '@/components/SideBar.vue'
import Main from '@/components/Main.vue'
import Modal from '@/components/Modal.vue'
export default {
  data () {
    return {
      showModal: false,
      eventDate: '',
      eventArea: '',
      eventDist: ''
    }
  },
  components: {
    Header,
    SideBar,
    Main,
    Modal
  },
  mounted () {
  },
  methods: {
    onModalStatusGet (status) {
      this.showModal = status
    },
    onEventDateGet (date) {
      this.eventDate = date
    },
    onEventAreaGet (area) {
      this.eventArea = area
    },
    onEventDistGet (dist) {
      this.eventDist = dist
    }
  }
}
</script>



