<template>
  <transition name="modal">
    <div class="modalMask">
      <div class="modalWrapper">
        <div class="modalContainer">
          <div class="unit">
            <div class="parameter">
              <h1>升級為 Instalaw Pro!</h1>
            </div>
            <div class="unitAction alignRight">
              <a href="javascript:;" @click="$emit('close')">
                關閉
              </a>
            </div>
          </div>
          <p>請選擇一個方案：</p>
          <div class="unit">
            <div class="parameter">
              <h3>基礎</h3>
              <h1>免費</h1>
              <ul>
                <li>判決時間預估</li>
                <li>判決結果預估</li>
              </ul>
            </div>
            <div class="parameter">
              <h3>
                <label><input type="radio" name="planSelector" checked>法律科技</label>
              </h3>
              <h1> NT $ 49</h1>
              <ul>
                <li>最相似判決時間分析</li>
                <li>最相似判決結果分析</li>
                <li>易科罰金與緩刑機率</li>
                <li>落點分析報告</li>
              </ul>
            </div>
            <div class="parameter">
              <h3>
                <label><input type="radio" name="planSelector">PAMO訂製</label>
              </h3>
              <h1>NT $ 1,499</h1>
              <ul>
                <li>最相似判決時間分析</li>
                <li>最相似判決結果分析</li>
                <li>易科罰金與緩刑機率</li>
                <li>
                  40分鐘律師實體電話諮詢，包含：
                  <ul>
                    <li>解構肇事責任判斷</li>
                    <li>完整法律流程提示</li>
                    <li>最佳訴訟策略建議</li>
                    <li>落點分析報告說明</li>
                  </ul>
                </li>
              </ul>
            </div>
          </div>
          <hr>
          <div>
            <h2>信用卡付款</h2>
            <div class="unit">
              <div class="parameter alignVerticleTop">
                <div class="unit">
                  <div class="unitAction alignVerticleCenter">
                    持卡人姓名
                  </div>
                  <div class="parameter">
                    <input type="text" class="w-100" placeholder="王大明" v-model="name" @keyup="checkValid">
                  </div>
                </div>
                <div class="unit">
                  <div class="unitAction alignVerticleCenter">
                    手機
                  </div>
                  <div class="parameter">
                    <input type="text" class="w-100" placeholder="091234567" v-model="phone" @keyup="checkValid">
                  </div>
                </div>
                <div class="unit">
                  <div class="unitAction alignVerticleCenter">
                    信用卡卡號
                  </div>
                  <div class="parameter">
                    <input type="text" class="w-100" placeholder="信用卡卡號" v-model="cardNum" @keyup="checkValid">
                  </div>
                </div>
                <div class="unit">
                  <div class="unitAction alignVerticleCenter">
                    卡片到期日
                  </div>
                  <div class="parameter">
                    <input type="text" class="w-100" placeholder="MM/YY" v-model="expireDate" @keyup="checkValid">
                  </div>
                </div>
                <div class="unit">
                  <div class="unitAction alignVerticleCenter">
                    CCV
                  </div>
                  <div class="parameter">
                    <input type="text" class="w-100" placeholder="安全驗證碼" v-model="cardccv" @keyup="checkValid">
                  </div>
                </div>
              </div>
              <div class="parameter alignVerticleTop">
                <div class="unit">
                  <div class="parameter">
                    <p>本公司不會留下您的信用卡資料，以確保您的權益。 每筆訂閱都會開立電子發票，並且同步寄送至您的電子信箱。</p>
                    <a href="javascript:;" class="btn main long" @click="sumbit" :class="{disabled: pass}">
                      付款，並升級服務
                    </a>
                  </div>
                </div>
                <div class="unit">
                  <div class="parameter">
                    <router-link to='/result' class="w-100">
                      <a href="javascript:;" class="btn long" @click="$emit('close')">
                        繼續使用免費服務
                      </a>
                    </router-link>
                  </div>
                </div>
              </div>
            </div>

          </div>

        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  methods: {
    sumbit () {
      alert('資料錯誤，請稍後再試')
    },
    checkValid () {
      if (this.name && this.phone && this.cardNum && this.expireDate && this.cardccv) {
        this.pass = false
      } else {
        this.pass = true
      }
    }
  },
  data () {
    return {
      name: '',
      phone: '',
      cardNum: '',
      expireDate: '',
      cardccv: '',
      pass: true
    }
  }
}

</script>



